<template>
  <v-row>
    <v-col
      cols="12"
      md="6"
      offset-md="3"
      class="pa-4"
    >
      <v-card>
        <v-card-text>
          <v-text-field
            :value="antyToken"
            :label="$t('browserExtension.token')"
            outlined
            dense
            hide-details
            class="anty-token-textfield"
            @click="selectInput($event)"
          >
            <template #append>
              <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <v-btn
                    icon
                    x-small
                    @click="copyToClipboard(antyToken)"
                    v-on="on"
                  >
                    <v-icon>content_copy</v-icon>
                  </v-btn>
                </template>
                <span>{{ $t('common.copy') }}</span>
              </v-tooltip>
            </template>
          </v-text-field>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>
<script>
import clipboard       from '@/mixins/clipboard';
import Cookies         from 'js-cookie';

export default {
  name: 'Anty',

  mixins: [clipboard],

  computed: {
    antyToken() {
      const data = {
        url: location.protocol + '//' + location.hostname + '/new',
        authorization: `${Cookies.get('user_id')}-${Cookies.get('hash')}`
      };
      return btoa(JSON.stringify(data));
    }
  },

  methods: {
    selectInput(event) {
      event.path[0].select();
    }
  }
};
</script>
<style>
.anty-token-textfield .v-input__append-inner {
  margin-top: auto !important;
  margin-bottom: auto !important;
}
</style>