<template>
  <div>
    <v-row class="mb-7">
      <v-col
        v-for="(news, index) in news.data"
        :key="`news-${news.id}`"
        class="my-1 col-12 col-sm-6 offset-sm-3"
      >
        <v-card
          class="mx-2 elevation-3"
        >
          <!-- ЗАГОЛОВОК НОВОСТИ -->
          <v-card-title class="d-flex justify-space-between">
            <!-- НАЗВАНИЕ -->
            <div v-html="newsTitle(news.title)" />

            <!-- ВРЕМЯ -->
            <div style="color: #9E9E9E; font-size: 12px;">
              <v-icon style="font-size: 12px;">
                far fa-clock
              </v-icon>
              {{ news.time }}
            </div>
          </v-card-title>

          <!-- ТЕЛО НОВОСТИ -->
          <v-card-text v-html="newsMessage(news.message)" />

          <!-- КНОПКИ -->
          <v-card-actions>
            <v-spacer />
            <v-btn
              :color="news.liked === false ? 'grey' : 'pink'"
              icon
              @click="setLike(news, index)"
            >
              <v-icon>
                {{ news.liked === false ? 'far fa-heart' : 'fas fa-heart' }}
              </v-icon>
            </v-btn>
            <span :style="news.liked === false ? 'color: #9E9E9E;' : 'color: #E91E63;'">
              {{ news.likes && news.likes > 0 ? news.likes : '' }}
            </span>
          </v-card-actions>
        </v-card>
      </v-col>
      <v-col
        v-if="!news.next !== null"
        cols="12"
        style="text-align: center;"
      >
        <v-btn
          color="primary"
          @click="loadMore(news.next)"
        >
          {{ $t('common.loadMore') }}
        </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import {mapGetters}              from 'vuex';

export default {
  name: 'News',

  computed: {
    ...mapGetters({
      profile: 'main/profile',
      news: 'news/news'
    })
  },

  async created() {
    await this.$store.dispatch('main/loadProfile');
    let data = {
      language: this.profile.language,
      lic: this.profile.lic,
      id: this.profile.id
    };
    await this.$store.dispatch('news/loadNews', data);
  },

  methods: {
    newsTitle(title) {
      return decodeURIComponent(atob(title).split('').map(function(c) {
        return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
      }).join(''));
    },

    newsMessage(message) {
      return decodeURIComponent(atob(message).split('').map(function(c) {
        return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
      }).join(''));
    },

    setLike(news, index) {
      if (news.liked === false) {
        this.$set(this.news.data[index], 'liked', true);
        this.$set(this.news.data[index], 'likes', news.likes + 1);
        this.api.get(`https://panel.dolphin.ru.com/?c=news&m=like&lic=${this.profile.lic}&dolphin_user_id=${this.profile.id}&news_id=${news.id}`);
      }
    },

    async loadMore(url) {
      await this.$store.dispatch('news/loadMore', url);
    }
  }
};
</script>
<style>
video {
  margin-top: 16px;
}

p, ul, ol {
  font-size: 18px;
}

div.v-card__text p:last-child {
  padding-bottom: 0;
}
</style>
